@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");

html,
body {
  font-family: Ubuntu, Arial;
  width: 100%;
  height: 100%;
  background: #fff;
}

#main {
  margin-left: 300px;
}

.shapedividers_com-3747 {
  overflow: hidden;
  position: relative;
}

.shapedividers_com-3747::before {
  content: "";
  font-family: "shape divider from ShapeDividers.com";
  position: absolute;
  bottom: -1px;
  left: -1px;
  right: -1px;
  top: -1px;
  z-index: 3;
  pointer-events: none;
  background-repeat: no-repeat;
  transform: scaleX(4.2);
  transform-origin: 100% 0;
  animation: 11s infinite alternate shape-anim-3747-mobile linear;
  background-size: 100% 92px;
  background-position: 50% 0%;
  background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.28 2.17" preserveAspectRatio="none"><path d="M0 .5c3.07.55 9.27-.42 16.14 0 6.88.4 13.75.57 19.14-.11V0H0z" fill="%2302152e"/><path d="M0 1c3.17.8 7.29-.38 10.04-.55 2.75-.17 9.25 1.47 12.67 1.3 3.43-.17 4.65-.84 7.05-.87 2.4-.02 5.52.88 5.52.88V0H0z" opacity=".5" fill="%2302152e"/><path d="M0 1.85c2.56-.83 7.68-.3 11.79-.42 4.1-.12 6.86-.61 9.58-.28 2.73.33 5.61 1.17 8.61 1 3-.19 4.73-.82 5.3-.84V.1H0z" opacity=".5" fill="%2302152e"/></svg>');
}

@keyframes shape-anim-3747-mobile {
  100% {
    transform: scaleX(4.2) translateX(calc(100% - (100% / 4.2)));
  }
}

@media (min-width: 768px) {
  .shapedividers_com-3747::before {
    transform: scaleX(4.2);
    transform-origin: 100% 0;
    animation: 11s infinite alternate shape-anim-3747-mobile linear;
    background-size: 100% 92px;
    background-position: 50% 0%;
  }
}
@media (max-width: 1025px) {
  .shapedividers_com-3747::before {
    display: none;
  }
}

@media (min-width: 1025px) {
  .shapedividers_com-3747::before {
    bottom: -0.1vw;
    left: -0.1vw;
    right: -0.1vw;
    top: -0.1vw;
    transform: scaleX(10);
    transform-origin: 100% 100%;
    animation: 5s infinite alternate shape-anim-3747 linear;
    background-size: 100% 57px;
    background-position: 50% 100%;
    background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.28 2.17" preserveAspectRatio="none"><path d="M35.28 1.67c-3.07-.55-9.27.41-16.15 0-6.87-.4-13.74-.58-19.13.1v.4h35.28z" fill="%23f7fafc"/><path d="M35.28 1.16c-3.17-.8-7.3.4-10.04.56-2.76.17-9.25-1.47-12.68-1.3-3.42.16-4.64.84-7.04.86C3.12 1.31 0 .4 0 .4v1.77h35.28z" opacity=".5" fill="%23f7fafc"/><path d="M35.28.31c-2.57.84-7.68.3-11.8.43-4.1.12-6.85.61-9.57.28C11.18.69 8.3-.16 5.3.02 2.3.22.57.85 0 .87v1.2h35.28z" opacity=".5" fill="%23f7fafc"/></svg>');
  }
}

@media (min-width: 2100px) {
  .shapedividers_com-3747::before {
    background-size: 100% calc(2vw + 57px);
  }
}

.theme-dark .shapedividers_com-3747::before {
  background-image: url('data:image/svg+xml;charset=utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.28 2.17" preserveAspectRatio="none"><path d="M35.28 1.67c-3.07-.55-9.27.41-16.15 0-6.87-.4-13.74-.58-19.13.1v.4h35.28z" fill="%23394450"/><path d="M35.28 1.16c-3.17-.8-7.3.4-10.04.56-2.76.17-9.25-1.47-12.68-1.3-3.42.16-4.64.84-7.04.86C3.12 1.31 0 .4 0 .4v1.77h35.28z" opacity=".5" fill="%23394450"/><path d="M35.28.31c-2.57.84-7.68.3-11.8.43-4.1.12-6.85.61-9.57.28C11.18.69 8.3-.16 5.3.02 2.3.22.57.85 0 .87v1.2h35.28z" opacity=".5" fill="%23394450"/></svg>');
}

@keyframes shape-anim-3747 {
  100% {
    transform: scaleX(10) translateX(calc(100% - (100% / 10)));
  }
}

section#inicio {
  background-image: url("./assets/bg-home.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  height: 100vh;
}

section#inicio h2 {
  font-size: 6rem;
}

section#inicio span#subtitle {
  margin-right: 8rem;
  font-size: 1.5rem;
}

.text-shadow {
  text-shadow: 4px 4px 10px #000000;
}
.shadowx {
  border-radius: 20px;
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
}

section#inicio div#arraste {
  width: 9rem;
}

section#inicio div#arraste a {
  text-decoration: none;
  color: #fff;
  margin-bottom: 15px;
}

i.menu-mobile {
  position: fixed;
  color: #fff;
  background-color: #02152e;
  right: 1rem;
  top: 1rem;
  font-size: 1.5rem;
  border-radius: 50%;
  cursor: pointer;
  z-index: 10;
  width: 40px;
  height: 40px;
  display: none;
  justify-content: center;
  align-items: center;
}

.menu-nav-active {
  overflow: hidden;
}
.menu-nav-active #header {
  left: 0;
}

@media (max-width: 1424px) {
  #header {
    left: -300px;
  }
  #footer {
    transition: all ease-in-out 0.5s;

    position: fixed;
    left: -300px;
  }

  i.menu-mobile {
    display: flex;
  }
  section#inicio h2 {
    font-size: 3.5rem;
  }
  section#inicio span#subtitle {
    margin-right: 0px;
    font-size: 1rem;
  }
  #main {
    margin-left: 0;
  }

  .card {
    width: 100%;
    height: 100%;
  }
}

.cardx {
  border-radius: 22px;
  background: #eff5ff;
  box-shadow: 20px 20px 60px #c0c0c0c4, -20px -20px 60px #ffffff93;
}
