.cv .cv-title {
  font-weight: 700;
  margin-top: 20px;
  margin-bottom: 20px;
  color: #02152e;
}
.cv .cv-item {
  padding: 0 0 20px 20px;
  margin-top: -2px;
  border-left: 2px solid #02152e;
  position: relative;
}
.cv .cv-item h4 {
  font-weight: 600;
  text-transform: uppercase;
  font-family: Poppins, sans-serif;
  color: #050d18;
  margin-bottom: 10px;
}
.cv .cv-item h5 {
  background: #e4edf9;
  padding: 5px 15px;
  display: inline-block;
  font-weight: 600;
  margin-bottom: 10px;
}
.cv .cv-item::before {
  content: "";
  position: absolute;
  height: 16px;
  width: 16px;
  border: 2px solid #02152e;
  background: #fff;
  border-radius: 50%;
  left: -9px;
}

.theme-dark .cv-item {
  border-left: 2px solid #fff;
}
.theme-dark .cv .cv-item::before {
  border: 2px solid #fff;
  background: #38434f;
}

.carousel-control-next {
  margin-right: -1rem !important;
}
.carousel-control-prev {
  margin-left: -1rem !important;
}

.carousel {
  display: flex;
  justify-content: center;
}
.carousel-inner {
  width: 55%;
  height: 55%;
}
.carousel-indicators {
  margin-bottom: -2rem !important;
}
.carousel-item > img {
  cursor: pointer;
  filter: blur(0px) !important;
  transition: 0.4s;
  z-index: 50;
}
@media (max-width: 500px) {
  .carousel-item > .carousel-caption > h5 {
    font-size: 0.6rem !important;
  }
  .carousel-item > .carousel-caption > p {
    font-size: 0.6rem !important;
  }
}

.carousel .carousel-item:hover > img {
  filter: blur(5px) !important;
}

.carousel-caption {
  width: 100%;
  bottom: -8px;
  transition: 0.3s;
}
.carousel-item:hover > .carousel-caption {
  bottom: 0px;
}

.carousel-item > .carousel-caption > h5 {
  opacity: 0;
  transition: 0.4s;
}

.carousel-item > .carousel-caption > div {
  opacity: 0;
  transition: 0.4s;
}
.carousel-item:hover > .carousel-caption > div {
  opacity: 1;
}

.carousel-item:hover > .carousel-caption > h5 {
  opacity: 1;
}
.carousel-item > .carousel-caption > p {
  opacity: 0;
  transition: 0.4s;
}

.carousel-item:hover > .carousel-caption > p {
  opacity: 1;
}

@media (max-width: 1424px) {
  .carousel-control-next {
    margin-right: 0rem !important;
  }
  .carousel-control-prev {
    margin-left: 0rem !important;
  }
  .carousel-inner {
    width: 55%;
    height: 55%;
  }
}

@media (max-width: 993px) {
  .carousel-inner {
    width: 70%;
    height: 70%;
  }
}
@media (max-width: 790px) {
  .carousel-inner {
    width: 85%;
    height: 85%;
  }

  .carousel-control-next {
    margin-right: -12px !important;
  }

  .carousel-control-prev {
    margin-left: -12px !important;
  }

  [data-aos="fade-left"] {
    transform: translate3d(-100%, 0, 0);
  }
}

