input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #bbe7d2 inset;
}

.form-control {
  border: solid 0px;
  height: 40px;
  border-radius: 5px;
  font-size: 16px;
  background-color: #e3ecf4;
  transition: 0.27s;
}

.form-control:focus-visible {
  background-color: #e7f0f6;
  box-shadow: 0 0 7px 0.2rem rgb(2 21 46 / 15%);
}

.form-control.fill:focus-visible {
  background-color: #a7e2c0b6;
  box-shadow: 0 0 7px 0.2rem rgb(2 21 46 / 15%);
}

.theme-dark .form-control {
  background-color: #3f4953 !important;
  transition: 0.27s;
}

.theme-dark .form-control:focus-visible {
  background-color: #404974 !important;
  box-shadow: 0 0 7px 0.2rem rgb(2 21 46 / 15%);
  transition: 0.2s;
}
.theme-dark input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #bbe7d2 inset;
}

.theme-dark .form-control.fill {
  background-color: #a7e2c0b6 !important;
  box-shadow: 0 0 7px 0.2rem rgb(2 21 46 / 15%);
  transition: 0.27s;
}

.form-control.l {
  height: 90px;
}
.fill {
  background-color: #a7e2c0b6;
}
.theme-dark .fill {
  background-color: #a7e2c0b6;
}

.bi-whatsapp {
  font-size: 1.6rem;
  position: fixed;
  bottom: 10px;
  right: 10px;
  color: #000000;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: 0.35s;
  z-index: 999999;
}

.bi-whatsapp:hover {
  background-color: #02152e;
  border-radius: 50%;
  color: #53cf5d;
}

.theme-dark .bi-whatsapp:hover {
  background-color: #53cf5d;
  color: #000000;
}
.theme-dark .bi-whatsapp {
