}

#footer {
  padding: 15px;
  color: #fff;
  font-size: 14px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 300px;
  z-index: 999999;
  background-color: #02152e;
  transition: all ease-in-out 0.5s;
}

@media (max-width: 1424px) {
  #footer {
    position: static;
    width: auto;
    padding: 20px 15px;
  }
  .bi-whatsapp {
    color: #53cf5dec;
  }
}

.theme-light body {
  transition: 0.2s;
}
.theme-dark body {
  color: #fff !important;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  transition: 0.2s;
}
.theme-dark h4 {
  color: #fff !important;
}
.theme-dark h5 {
  color: #000 !important;
}
.theme-dark h5.white {
  color: #fff !important;
}
.theme-dark label {
