.theme-dark {
  --dark-text: #292929;
  --light-background: #586f7c;
  --accent: #b8dbd9;
  --button-border: #b8dbd9;
  --light-text: #f9f8f8;
  transition: 0.3s;
}
.theme-dark .section-bg {
  color: #fff;
  background-color: #333c66;
}
.theme-dark .card {
  background-color: var(--dark-background) !important;
}

.theme-dark .btn-primary {
  background-color: #e94646 !important;
  border-color: #212529;
  color: #fff;
  transition: 0.4s !important;
}

.theme-dark .btn-primary:hover {
  background-color: #af3838 !important;
  color: #fff;
}

.theme-dark .form-control {
  background-color: #484a4d;
  color: #fff;
}
.theme-dark .cardx {
  box-shadow: 20px 20px 60px #1d1d1d9c, -20px -20px 60px #05050583;
}

.theme-dark .shadowx {
  box-shadow: 20px 20px 60px #1d1d1d7c, -20px -20px 60px #0505056c;
}
